/* 1. Header */
header {
    position: fixed;
    left: 0;
    top: 15px;
    width: 100%;
    z-index: 100;
    text-align: right;
    background: $bg;
    opacity: 1;
    visibility: visible;
    @include transition(all 0.6s ease 0s);

    &.active {
        opacity: 1!important;
        visibility: visible!important;
        .menu-btn {
            &:before {
                opacity: 0;
            }
            &:after {
                bottom: 9px!important;
                @include transform(rotate(-45deg));
            }
            span {
                @include transform(rotate(45deg));
            }
        }
        .head-top {
            .top-menu {
                position: fixed;
                top: 100px;
                left: 50%;
                margin-left: -100px;
                width: 200px;
                height: auto;
                text-align: center;
                opacity: 1!important;
                visibility: visible!important;
                ul {
                    li {
                        display: block;
                        margin: 30px 0;
                    }
                }
            }
        }
    }
    @media (max-width: 840px) {
        //top: 15px;
    }
    .head-top {
        padding: 15px 65px 15px 65px;
        .menu-btn {
            position: relative;
            margin-left: auto;
            width: 26px;
            height: 20px;
            &:before, &:after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 2px;
                background: $dark-color;
                @include transition(all 0.3s ease 0s);
            }
            &:after {
                top: auto;
                bottom: 0;
            }
            span {
                position: absolute;
                top: 50%;
                left: 0;
                width: 100%;
                height: 2px;
                background: $dark-color;
                margin-top: -1px;
                @include transition(all 0.3s ease 0s);
            }
            &:hover {
                &:before, &:after, span {
                    background: $active-color;
                }
            }
            @media (max-width: 840px) {
                display: block;
            }
        }
        .top-text {
          float: left;
          font-size: 18px;
          position: absolute;
          top: 15px;
        }

        .top-menu {
            @media (max-width: 840px) {
                opacity: 0;
                visibility: hidden;
                height: 0px;
                @include transition(opacity 0.3s ease 0.5s);
            }
            ul {
                li {
                    margin-left: 50px;
                    display: inline-block;
                    vertical-align: top;
                    @include transition(color 0.3s ease 0s);
                    a {
                        display: block;
                        margin-top: 2px;
                        &.btn {
                            margin-top: 0;
                        }
                    }
                    &.active {
                        a {
                            color: $active-color;
                        }
                        .btn {
                            color: $bg;
                            background: $active-color;
                            border: 1px solid $active-color;
                        }
                    }
                }
            }
        }
    }
}

/* 2. Section */
.section {
    position: relative;
    padding: 0 80px 150px 80px;
    z-index: 97;
    opacity: 1;
    visibility: visible;
    @include transition(all 0.6s ease 0s);
    @media (max-width: 960px) {
        padding: 0 40px 120px 40px;
    }
    @media (max-width: 480px) {
        padding: 0 20px 120px 20px;
    }
    .content {
        position: relative;
        .title {
            position: relative;
            margin-bottom: 40px;
            @media (max-width: 400px) {
                text-align: center;
            }
            .title_inner {
                display: inline-block;
                vertical-align: middle;
                text-align: center;
                position: relative;
                line-height: 18px;
                font-size: $default-size;
                color: $dark-color;
                text-transform: uppercase;
                letter-spacing: $extra-letter-spacing;
                @include transition(all 0.3s ease 0s);
                @include box-shadow(inset 0 -6px 0px $extra-active-color);
            }
        }
    }
}

/* 3. Banner */
.section.banner {
    text-align: center;
    top: 50px;
    position: relative;
    height: 150px;
    // background-image: url('/assets/images/banner.png');
    // background-size: cover;
    padding: 0;
    margin: 0;
    .banner-content {
        display: inline-block;
        vertical-align: middle;
        text-align: center;
        position: relative;

        .banner-title {
            font-size: 48px;
            font-family: $default-font;
            color: #999; // #fff;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: $extra-letter-spacing;
            margin: 10px 0 15px 0;

            @media (max-width: 1060px) {
                &.blog_title {
                    font-size: 48px;
                }
            }
            @media (max-width: 840px) {
                font-size: 52px;
                padding: 0 20px;
                &.blog_title {
                    font-size: 40px;
                }
            }
            @media (max-width: 580px) {
                span {
                    display: block;
                }
            }
            @media (max-width: 480px) {
                font-size: 42px;
                &.blog_title {
                    font-size: 26px;
                }
            }
        }

        .banner-subtitle {
            font-size: $default-size;
        }
    }
}

/* 3. Started */
.section.started {
    text-align: center;
    padding: 0;
    margin: 0;
    position: relative;

    .started-content-bg {
        display: inline-block;
        vertical-align: middle;
        text-align: center;
        position: relative;
        background: #31313a;
        width: 100%;
    }

    .started-content {
        display: inline-block;
        vertical-align: middle;
        text-align: center;
        position: relative;

        .h-title {
            font-size: $title-size;
            font-family: $default-font;
            color: $dark-color;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: $extra-letter-spacing;

            @media (max-width: 1060px) {
                &.blog_title {
                    font-size: 48px;
                }
            }
            @media (max-width: 840px) {
                font-size: 52px;
                padding: 0 20px;
                &.blog_title {
                    font-size: 40px;
                }
            }
            @media (max-width: 580px) {
                span {
                    display: block;
                }
            }
            @media (max-width: 480px) {
                font-size: 42px;
                &.blog_title {
                    font-size: 26px;
                }
            }
        }

        .h-subtitle {
          margin-top: -30px;
          margin-bottom: 0;
          font-size: $default-size;
        }

        .typed-subtitle, .typed-bread {
          font-size: $default-size;
        }
    }

}



/* 4. About */
.section.about {
    @media (max-width: 400px) {
        text-align: center;
    }
    .image {
        float: left;
        img {
            width: 100px;
            height: 100px;
            @include border-radius(100px);
        }
        @media (max-width: 600px) {
            img {
                width: 80px;
                height: 80px;
                @include border-radius(80px);
            }
        }
        @media (max-width: 400px) {
            float: none;
            text-align: center;
            img {
                width: 100px;
                height: 100px;
                @include border-radius(100px);
            }
        }
    }
    .desc {
        margin-left: 130px;
        @media (max-width: 600px) {
            margin-left: 100px;
        }
        @media (max-width: 400px) {
            margin-left: 0;
        }
    }
}

/* 4. About */
.section.press {
    overflow: hidden;
    position: relative;
    margin: auto;
    top: 80px;
    bottom: 0px;
    max-width: 1060px;
    text-align:center;
    font-size: 14px;
    color: #eee;
    .h3 {
      color: $default-color;
    }
    @media (max-width: 400px) {
        text-align: center;
    }
    .image {
        float: left;
        img {
            width: 100px;
            height: 100px;
            @include border-radius(100px);
        }
        @media (max-width: 600px) {
            img {
                width: 80px;
                height: 80px;
                @include border-radius(80px);
            }
        }
        @media (max-width: 400px) {
            float: none;
            text-align: center;
            img {
                width: 100px;
                height: 100px;
                @include border-radius(100px);
            }
        }
    }
    .desc {
        margin-left: 130px;
        @media (max-width: 600px) {
            margin-left: 100px;
        }
        @media (max-width: 400px) {
            margin-left: 0;
        }
    }
}

.info-list {
    margin-bottom: 42px;
    ul {
        li {
            display: inline-block;
            vertical-align: top;
            width: 32%;
            margin: 12px 0 0 0;
            @media (max-width: 840px) {
                width: 48%;
            }
            @media (max-width: 580px) {
                width: 100%;
            }
            strong {
                font-weight: 400;
                color: $active-color;
            }
        }
    }
}

/* 5. Resume */
@media (max-width: 840px) {
    .section.resume {
        .cols {
            .col.col-md {
                &:first-child {
                    margin-bottom: 120px;
                }
            }
        }
    }
}

.resume-items {
    .resume-item {
        position: relative;
        padding: 0 30px 60px 30px;
        @media (max-width: 840px) {
            padding-right: 0px;
        }
        &:before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 1px;
            height: 100%;
            background: $active-color;
        }
        &:first-child {
            padding-top: 0;
        }
        &:last-child {
            padding-bottom: 0;
            margin-top: -1px;
            &:before {
                display: none;
            }
        }
        .date {
            position: relative;
            top: -10px;
            margin: 0 0 5px 0;
            display: inline-block;
            padding: 0 5px;
            height: 20px;
            line-height: 18px;
            font-weight: 500;
            font-size: $extra-small-size;
            color: $active-color;
            border: 1px solid $active-color;
            &:before {
                content: '';
                width: 29px;
                height: 1px;
                position: absolute;
                left: -30px;
                top: 9px;
                background: $active-color;
            }
        }
        .name {
            font-weight: 500;
            color: $dark-color;
            margin: 0 0 15px 0;
        }
        p {
            margin: 0;
        }
    }
}

/* 6. Skills */
.skills {
    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        li {
            padding: 5px 0 30px 0;
            .name {
                font-size: $default-size;
                color: $default-color;
                font-weight: 400;
                position: relative;
                margin: 0 0 9px 0;
            }
            .progress {
                display: block;
                height: 3px;
                position: relative;
                width: 100%;
                background: $light-color;
                .percentage {
                    left: 0;
                    top: 0;
                    position: absolute;
                    height: 3px;
                    width: 0%;
                    background: $active-color;
                    @include transition(all 0.3s ease 0s);
                    .percent {
                        position: absolute;
                        top: -27px;
                        right: 0;
                        font-size: $default-size;
                        color: $default-color;
                        font-weight: 400;
                    }
                }
            }
            &:last-child {
                padding-bottom: 0;
            }
        }
    }
}

/* 7. Service */
.section.service {
    @media (max-width: 400px) {
        text-align: center;
    }
}

.section.service, .section.contacts {
    .title {
        margin-bottom: 0;
    }
}

.service-items {
    margin: 0 -20px;
    font-size: 0px;

    .service-item {
        display: inline-block;
        vertical-align: top;
        width: 33.3%;
        margin: 50px 0 0 0;
        text-align: left;
        padding: 0 20px;

        @media (max-width: 840px) {
            width: 45%;
        }
        @media (max-width: 580px) {
            width: 100%;
        }
        @media (max-width: 400px) {
            text-align: center;
        }

        .icon {
            float: left;
            width: 36px;
            text-align: center;
            font-size: 30px;
            color: $active-color;
            @media (max-width: 400px) {
                float: none;
                display: inline-block;
                vertical-align: middle;
            }
            .ion {
                display:block;
            }
        }
        .name {
            font-weight: 500;
            font-size: $default-size;
            color: $dark-color;
            margin: 6px 0 0 46px;
            @media (max-width: 400px) {
                margin: 0 0 0 2px;
                display: inline-block;
                vertical-align: middle;
            }
        }
        p {
            margin-bottom: 0;
        }
    }
}

/* 8. Portfolio */
.section.works {
  .description{
    overflow: hidden;
    position: relative;
    margin: auto;
    top: 80px;
    bottom: 0px;
    max-width: 1060px;
    text-align: center;
    font-size: 14px;
    color: #eee;
  }

  .filters {
        margin-top: 80px;
        margin-bottom: 30px;
        input {
            display: none;
        }
        label {
            display: inline-block;
            vertical-align: top;
            margin-right: 25px;
            font-size: $default-size;
            color: $default-color;
            cursor: pointer;
            position: relative;
            padding-bottom: 0;
            &.glitch-effect {
                color: $active-color;
            }
            @media (max-width: 580px) {
                padding-bottom: 15px;
            }
        }
        @media (max-width: 580px) {
            margin-bottom: 10px;
        }
    }
}

/* 9. Box Items */
.box-items {
    position: relative;
    overflow: hidden;
    margin-left: -3%;
    @media (max-width: 580px) {
        margin-left: 0;
    }
    .box-item {
        width: 30.33333%;
        margin: 0 0 3% 3%;
        position: relative;
        overflow: hidden;
        text-align: center;
        @media (max-width: 840px) {
            width: 47%;
        }
        @media (max-width: 580px) {
            width: 100%;
            margin: 0 0 3% 0;
        }
        // &:hover {
        //     .image {
        //         .info {
        //             opacity: 0.94;
        //             @include transform(scale(1,1));
        //         }
        //     }
        //     .desc {
        //         .name {
        //             color: $active-color;
        //         }
        //     }
        // }
        .image {
            position: relative;
            a {
                display: block;
                font-size: 0;
                img {
                    width: 100%;
                    height: auto;
                    position: relative;
                    top: 0;
                }
            }
            .info {
                text-align: center;
                width: 100%;
                height: 100%;
                display: block;
                position: absolute;
                left: 0;
                top: 0;
                opacity: 0;
                padding: 18px;
                @include transition(all 0.3s ease 0s);
                @include transform(scale(0.5,0.5));
                .centrize {
                    background: $active-color;
                }
                .ion {
                    color: $white-color;
                    font-size: 38px;
                    display: inline-block;
                    @include transition(all 0.1s ease-in 0.4s);
                    font-weight: normal;
                }
            }
        }
        .desc {
            position: relative;
            padding: 15px 45px 5px 45px;
            @media (max-width: 1000px) {
                padding: 15px 15px 5px 15px;
            }
            .name {
                height: 40px;
                display: block;
                color: $default-color;
                font-size: $default-size;
                font-weight: 700;
                text-transform: uppercase;
                @include transition(all 0.3s ease 0s);
            }
        }
    }
}

.date {
    margin: 0 0 10px 0;
    display: inline-block;
    padding: 0 5px;
    height: 20px;
    line-height: 18px;
    font-weight: 500;
    font-size: $extra-small-size;
    color: $active-color;
    border: 1px solid $active-color;
}

.category {
    margin: 0 0 10px 0;
    display: inline-block;
    font-size: $extra-small-size;
    color: $default-color;
    text-transform: uppercase;
    letter-spacing: $extra-letter-spacing;
    // @include box-shadow(inset 0 -6px 0px $extra-active-color);
}

/* 10. Contacts */
.section.contact {
    overflow: hidden;
    position: relative;
    margin: auto;
    top: 60px;
    max-width: 1060px;
    font-size: 14px;
    color: #eee;
    text-align: center;

    @media (max-width: 400px) {
        text-align: center;
    }
    .service-items {
        .service-item {
            p {
                margin-top: 20px;
            }
        }
    }
    .contact_form {

        margin-top: 30px;
    }
    .alert-success {
        display: none;
        p {
            margin: 60px 0 30px 0;
            font-size: $large-size;
        }
    }
}

/* 11. Blog */
.post-image {
    margin: 0 0 25px 0;
    font-size: 0;

    img {
        max-width: 100%;
        height: auto;
    }
}

.single-post-text {
    font-size: $default-size;
    line-height: 23px;

    &:after {
        content: '';
        position: relative;
        clear: both;
        display: block;
    }

    img {
        max-width: 100%;
        height: auto;
    }

    p {
        a {
            color: $active-color;

            &:hover {
                text-decoration: underline;
            }
        }
    }

    blockquote {
        margin: 25px 0;
        padding: 0 0 0 20px;
        font-size: $large-size;
        line-height: 24px;
        color: $extra-color;
        font-style: italic;
        font-weight: 300;
        border-left: 2px solid $active-color;

        code {
            padding: 2px;
            display: inline;
        }
    }

    iframe, object {
        max-width: 100%;
    }

    ul, ol {
        li {
            margin: 5px 0;
            padding: 0 0 0 18px;
            position: relative;
        }
    }

    ol {
        list-style-position: inside;

        & > li {
            margin: 5px 0;
            padding: 0;
            position: relative;
        }
    }

    ul {
        list-style: none;

        li {
            &:before {
                margin: 0;
                content: '';
                position: absolute;
                left: 0;
                top: 8px;
                width: 4px;
                height: 4px;
                background: $active-color;
                border-radius: 4px;
                -webkit-border-radius: 4px;
            }
        }
    }

    figure {
        margin: 0 0 30px 0;
        max-width: 100%;
    }
}

.post-text-bottom {
    padding: 25px 0 10px 0;

    .share-btn {
        position: relative;
        display: inline-block;
        top: 1px;
        margin-left: 7px;
        margin-right: 7px;
        font-size: 16px;
    }
    & > div, & > .social-share {
        margin-right: 30px;
        margin-bottom: 20px;
        display: inline-block;
        vertical-align: middle;
    }
    & > .cat-links, & > .byline, & > .social-share {
        font-size: $small-size;
        color: $extra-color;
    }
    .cat-links {
        a {
            color: $active-color;

            &:hover {
                text-decoration: underline;
            }
        }
    }
    .tags-links {
        display: block;
        line-height: 18px;
        font-size: 0px;

        span {
            display: inline-block;
            font-size: $small-size;
            color: $extra-color;
            margin-right: 10px;
        }

        a {
            margin: 0 10px 10px 0;
            display: inline-block;
            padding: 0 5px;
            height: 20px;
            line-height: 18px;
            font-weight: 500;
            font-size: $extra-small-size;
            color: $active-color;
            border: 1px solid $active-color;
        }
    }
    .author {
        color: $default-color;
    }
}

.post-navigation {
    color: $extra-color;
    text-align: center;

    &:after {
        content: '';
        display: block;
        clear: both;
    }

    a {
        position: relative;
        font-size: $small-size;
        font-weight: 400;
        text-transform: uppercase;
        color: #aca3a3;
        line-height: 20px;
        position: relative;
        padding: 5px 10px;
        display: inline-block;
        -webkit-transition: all .2s ease-out 0s;
        transition: all .2s ease-out 0s;
        opacity: 0.7;

        &:hover {
            opacity: 1;
        }

        &:before, &:after {
            margin-top: -10px;
            position: absolute;
            content: '';
            left: 0;
            top: 50%;
            display: block;
            width: 20px;
            height: 20px;
            background: url(../images/pag.png) no-repeat center center;
            -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
        }
    }

    .nav-previous {
        a {
            float: left;
            padding-left: 34px;

            &:after {
                display: none;
            }
        }
    }
    .nav-next {
        a {
            float: right;
            padding-right: 34px;

            &:before {
                display: none;
            }
            &:after {
                left: auto;
                right: 0;
                -webkit-transform: scaleX(1);
                transform: scaleX(1);
            }
        }
    }

}

.post-comments {
    margin-top: 60px;
    .post-comment {
        padding: 20px 0;
        border-top: 1px solid $light-color;
        &:first-child {
            padding-top: 0;
            border-top: none;
        }
        .image {
            float: left;
            width: 80px;
            height: 80px;
            img {
                width: 100%;
                height: 100%;
            }
        }
        .desc {
            margin-left: 100px;
            .name {
                font-size: $default-size;
                color: $dark-color;
                text-transform: uppercase;
                font-weight: 500;
                padding: 5px 0 0 0;
                margin: 0 0 10px 0;
            }
            p {
                margin: 0;
            }
        }
    }
    .form-comment {
        .title {
            margin: 30px 0 15px 0;
        }
    }
}

/* 12. Footer */
footer {
    position: fixed;
    bottom: 15px;
    left: 0;
    padding: 15px 60px 15px 60px;
    width: 100%;
    background: $bg;
    z-index: 100;
    opacity: 1;
    visibility: visible;
    @include transition(all 0.6s ease 0s);
    @media (max-width: 840px) {
        bottom: 15px;
    }
    .soc {
        float: right;
        a {
            display: inline-block;
            vertical-align: middle;
            margin-left: 12px;
            .ion {
                font-size: 17px;
                color: $default-color;
                @include transition(all 0.3s ease 0s);
            }
            &:hover {
                .ion {
                    color: $active-color;
                }
            }
        }
        @media (max-width: 840px) {
            float: none;
            text-align: center;
            a {
                margin: 0 6px;
            }
        }
    }
    .copy {
        float: left;
        padding-top: 2px;
        font-size: $extra-small-size;
        color: $extra-color;
        @media (max-width: 840px) {
            display: none;
        }
    }
}

/* 13. Popups */
.popup-box {
    margin: 30px auto;
    width: 520px;
    background: $white-color;
    position: relative;
    padding: 10px;
    @media (max-width: 580px) {
        width: 440px;
    }
    @media (max-width: 480px) {
        width: 320px;
    }
    .image {
        img {
            width: 100%;
            height: auto;
        }
    }
    .desc {
        padding: 30px 20px;
        h4 {
            display: block;
            color: $default-color;
            font-size: $large-size + 2;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: $extra-letter-spacing;
            margin: 0 0 15px 0;
            line-height: normal;
        }
        p {
            margin-top: 15px;
        }
    }
}

.mfp-fade.mfp-bg {
    opacity: 0;
    @include transition(all 0.15s ease-out);
}

/* overlay animate in */
.mfp-fade.mfp-bg.mfp-ready {
    opacity: 0.8;
}

/* overlay animate out */
.mfp-fade.mfp-bg.mfp-removing {
    opacity: 0;
}

/* content at start */
.mfp-fade.mfp-wrap .mfp-content {
    opacity: 0;
    @include transition(all 0.15s ease-out);
}

/* content animate it */
.mfp-fade.mfp-wrap.mfp-ready .mfp-content {
    opacity: 1;
}

/* content animate out */
.mfp-fade.mfp-wrap.mfp-removing .mfp-content {
    opacity: 0;
}

.mfp-close {
    width: 54px!important;
    height: 54px!important;
    background: $white-color!important;
    line-height: 54px!important;
    opacity: 1!important;
    font-weight: 300;
    color: $white-color;
    font-size: 24px;
    font-family: 'Verdana'!important;
}



